:root {
  --line: rgb(255, 255, 255, 0.35);
  --highlight: rgb(255, 255, 255, 0.15);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 1rem;
  color: white;
  text-align: left;
}

body {
  background-color: black;
}

.App {
  font-family: sans-serif;
  width: 80%;
  max-width: 1000px;
  margin: 0 auto;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.25rem;
  margin: 0.25rem 0;
}

label {
  margin-left: 0.75rem;
}

input[type="radio"] {
  margin: 0.35rem 0.5rem 0 0;
}

.notification {
  border-radius: 0.5rem;
  padding: 1rem 1.5rem;
  border-left: 0.5rem solid darkorchid;
  background-color: var(--highlight);
}

.success {
  border-left: 1rem solid greenyellow;
}

li {
  list-style-position: inside;
}

.box {
  display: flex;
}

.column-one {
  flex: 1;
  text-align: left;
}
.column-two {
  flex: 2;
}

select {
  width: 100%;
  padding: 0.5rem;
}

input[type="submit"] {
  border-radius: 0.5rem;
  padding: 0.75rem 1rem;
  border: none;
  background-color: darkorchid;
  color: black;
}

input[type="submit"],
select {
  cursor: pointer;
}

table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid var(--line);
}

th {
  background-color: var(--highlight);
}
th,
td {
  padding: 0.4rem;
}
th:not(:last-child),
td:not(:last-child) {
  border-right: 1px solid var(--line);
}
tr {
  border-bottom: 1px solid var(--line);
}

.button-row {
  width: 100%;
  display: flex;
  justify-content: end;
}

h1,
.notification,
.box {
  margin: 2rem 0;
}

button,
select {
  background-color: var(--highlight);
}

table,
.alert {
  margin-top: 0.5rem;
}

@media screen and (max-width: 600px) {
  .box {
    display: block;
  }
  .column-one {
    margin-bottom: 0.75rem;
  }
}
